.auth-page-container {
  display: flex;
  /* Calculate height to fill screen below the header */
  min-height: calc(100vh - 72px); /* Adjust 72px to match your header's height */
  width: 100%;
  align-items: stretch;
}

.info-panel {
  flex-basis: 55%;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  box-sizing: border-box;
  text-align: center;
}

.info-content h2 {
  font-size: 2.5rem;
  color: var(--accent-color);
  margin-bottom: 1rem;
}

.info-content p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 450px;
  line-height: 1.6;
}

.form-panel {
  flex-basis: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--surface-color);
  padding: 40px;
  box-sizing: border-box;
}

/* Responsive design for smaller screens */
@media (max-width: 850px) {
  .auth-page-container {
    flex-direction: column;
  }
  .info-panel {
    min-height: 200px;
    justify-content: flex-end;
    padding-bottom: 2rem;
  }
  .form-panel {
    padding: 2rem 1rem;
  }
}